const createGalleryCard = infoCard => {
    const { preview, original, description } = infoCard;
    return `<li class="gallery-item">
    <a class="gallery-link" href="${original}">
    <img
        class="gallery-image"
        src="${preview}"
        alt="${description}"
    />
    </a>
</li>
`
}
const galleryCardsTemplate = images.map(image => createGalleryCard(image)).join('');
const galleryListEl = document.querySelector('.js-gallery');
galleryListEl.insertAdjacentHTML('afterbegin', galleryCardsTemplate);
const lightbox = new SimpleLightbox('.gallery-link', {
    captions: true,             
    captionsData: 'alt',        
    captionPosition: 'bottom',  
    captionDelay: 250
});
